@media only screen and (min-width: 64.063em) {
  header {
    margin-top: 20px; } }

#uiowa-bar #uiowa-search .links a {
  font-size: 0.9em; }

.big-header-container {
  max-width: 600px;
  margin: auto; }

.big-header {
  text-align: center; }
  .big-header h1 {
    padding: 10px 0; }
  .big-header .tagline {
    font-size: 18px;
    text-transform: uppercase;
    margin-bottom: 0;
    color: white;
    background: #DD7064; }

/* Home Page specific big header styles */
.HomePage .big-header-container {
  max-width: 1000px; }

.HomePage .big-header .tagline {
  font-size: 26px; }

.main-header h1, .main-header h2 {
  display: inline; }

.main-header h2 {
  color: #A5A3A3; }

.main-header h2 {
  margin-left: 20px; }

@media only screen and (min-width: 40.063em) {
  .top-bar {
    text-align: center; } }

.top-bar .name img {
  width: 300px; }

@media only screen and (min-width: 40.063em) {
  .top-bar-section {
    display: inline-block; } }

.top-bar-section ul {
  background: none;
  text-transform: lowercase; }

.top-bar-section ul.left li {
  font-size: 1.125rem; }
  @media only screen and (min-width: 64.063em) {
    .top-bar-section ul.left li {
      font-size: 1.5625rem; } }

@media only screen and (min-width: 40.063em) {
  .top-bar-section ul.right.top-menu-items li {
    margin-top: 1.3em; } }

.top-bar-section li:not(.has-form) a:not(.button):not(input.action):hover {
  text-decoration: none; }

.top-bar-section ul.right.top-menu-items .active {
  background: none repeat scroll 0 0 rgba(37, 41, 49, 0.65); }

.top-bar-section ul li > a {
  text-transform: lowercase; }
  @media only screen and (min-width: 40.063em) {
    .top-bar-section ul li > a {
      text-shadow: 0 0 40px #333; } }
  @media only screen and (min-width: 64.063em) {
    .top-bar-section ul li > a {
      font-size: 1.375rem; } }

.top-bar-section li:not(.has-form) a.tagline:not(.button):not(input.action) {
  background: none; }

.top-bar-section ul li > a.tagline {
  font-size: 1rem;
  text-transform: uppercase;
  color: white; }

/* overriding this because foundation 5.2.3 broke the hamburger icon? */
body .main.HomePage {
  background: rgba(34, 37, 44, 0.25);
  color: white;
  padding-top: 15px; }

.HomePage .event-list li {
  height: none; }
  @media only screen and (min-width: 40.063em) {
    .HomePage .event-list li {
      height: none; } }
  @media only screen and (min-width: 64.063em) {
    .HomePage .event-list li {
      height: none; } }

#top-content #next-up {
  margin-bottom: 10px;
  margin-top: 0px; }
  @media only screen and (min-width: 40.063em) {
    #top-content #next-up {
      margin-top: 0px; } }
  #top-content #next-up h2 {
    font-size: 2.5rem; }
  #top-content #next-up ul, #top-content #next-up ol {
    margin: 0; }
  #top-content #next-up a {
    font-size: 1em;
    color: #fff; }
  #top-content #next-up a:hover {
    text-decoration: none; }
  #top-content #next-up #next-up-list li {
